div#products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(20rem, 100%), 1fr));
  grid-gap: 1rem;
  grid-auto-flow: dense;
}

img.product__image {
  max-width: 300px;
  max-height: 350px;
}

.product__card {
  width: -webkit-fill-available;
  text-align: center;
  padding: 2rem;
  background: rgb(254 254 214 / 46%);
}


.feed__box {
  padding: 2rem;
  background: whitesmoke;
  margin-top: 2%;
  width: -webkit-fill-available;
  /* margin: 0 auto; */
  text-align: center;
}

.feed__container {
  display: flex;
  justify-content: center;
  /* flex-direction: row; */
  width: -webkit-fill-available;
}
input.feed__text {
  /* width: 500px; */
  /* height: 199px; */
  padding: 0.8rem 1.2rem;
  width: 300px;
  height: 50px;
  margin-right: 2%;
  margin-top: 1%;
  /* border: 0px; */
}

.post {
  background: whitesmoke;
  width: 500px;
  margin-top: 2% !important;
  margin: 0 auto;
}

p.post-bod {
  padding: 1rem;
  font-weight: 200;
  background: rgba(0,0,0,0.75);
  color: #fff;
  
}

.form-ctrl {
  display: inline-block !important;
  gapp: ;
  padding: 1%;
}

button.feed__submit {
  padding: 0.6rem 1.8rem;
  margin: 2%;
  border: ;
  background: transparent;
  border: 1px solid rgb(0 0 0 / 69%);
}


.posts.container.is-fluid {
  background: pink;
  width: fit-content;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
  border-radius: 7px;
  margin-bottom: 4%;

}

p.post_tit {
  font-weight: 600;
  margin-bottom: 1.5%;
  -webkit-font-smoothing: subpixel-antialiased;
}
.post_head {
  padding: 2%;
  margin: 1%;
  border-bottom: 1px dashed rgb(132 129 129 / 55%);

}

p.post-note.block {
  font-weight: 300;
  font-size: 14px;
  word-break: break-word;
  white-space: break-spaces;
  
}

span.is-pulled-right.delBut {
  font-weight: 600;
  color: #000;
  padding: 2px;
  transition: ease opacity 0.6s;
  opacity: 0.6;
  cursor: pointer;
}

span.is-pulled-right.delBut:hover {
  opacity: 1;
}






@media screen and (max-device-width: 500px)  { 
  /* STYLES HERE */
  .posts.container.is-fluid {
    border-radius: 0px;
  }
}